export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule } from \"@angular/router\";\nimport { DEMO_PATH_TOKEN, NuiDocsModule, NuiSelectorModule } from \"@nova-ui/bits\";\n\nimport { SelectorExampleComponent } from \"./selector.example.component\";\n\nconst routes = [{\n    path: \"\",\n    component: SelectorExampleComponent,\n}];\n\n@NgModule({\n    imports: [\n        NuiSelectorModule,\n        NuiDocsModule,\n        RouterModule.forChild(routes),\n    ],\n    declarations: [\n        SelectorExampleComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class SelectorModule {\n}\n";